@import url('https://fonts.googleapis.com/css2?family=Concert+One&family=Permanent+Marker&display=swap');

.section-1 {
    min-height: 120vh;
    display: flex;
    align-items: center;
    font-family: sans-serif;
    position: relative;
}

.intro-page h1 {
    font-size: 2.5em;
    line-height: 2rem;
    letter-spacing: -1px;
    font-weight: 700;
    color: var(--color-tem);
    font-family: 'Helvetica Neue', sans-serif;
}


.intro-page h1 .home-logo {
    width: 30px;
    animation: fadeIn 2s linear;
    // animation-delay: 1.4s;
    fill: var(--color-tem);
    display: inline-block;
    margin-inline: 25px;


    .logo {
        width: 80px;
        height: 80px;
        border: none;
        border-radius: 23px;
        display: flex;
        align-items: center;
        position: relative;
        z-index: 100;

        span {
            font-family: 'Concert One', cursive;
            font-style: italic;
            font-weight: bold;
            font-size: 95px;
            position: absolute;
            left: 16px;
            bottom: 20px;
            color: white;
        }

        &:hover {
            animation: rubberBand 1s;
            animation-timing-function: all 250ms ease-in-out;
        }
    }
}

.animate-logo-1 {
    transition: 0.3s linear;
    animation: logo 2s cubic-bezier(0.075, 0.82, 0.165, 1) infinite;
}

.animate-logo-2 {
    animation: logo 7s cubic-bezier(0.075, 0.82, 0.165, 1) infinite;
}

@keyframes logo {
    0% {
        transform: translateY(-20px);
    }

    50% {
        transform: translateY(40px);
    }
}

.section-1 .text-desc {
    font-size: 1em;
    margin: 30px 0;
    letter-spacing: 2px;
    animation: fadeIn 1s 1.4s backwards;
}

.contact-button {
    display: block;
    width: 100%;
    height: 50px;
    max-width: 200px;
    line-height: 50px;
    position: relative;
    margin: 50px 0;
    animation: fadeInAnimation 1s 1.8s backwards;
    white-space: nowrap;
    z-index: 100;
}

.contact-button div {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    border-radius: 0 15px 0 15px;
}

.contact-button span.bg {
    width: 0%;
    height: 100%;
    position: absolute;
    left: -5%;
    top: 0;
    transform: skewX(-10deg);
    transition: all 0.3s linear;
}

.contact-button:hover span.bg {
    background: var(--color-tem);
    width: 110%;
    border-radius: 0 15px 0 15px;
}

.contact-button span.base {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 0 15px 0 15px;
    border: 1px solid var(--color-tem);
}

.contact-button span.text {
    text-align: center;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
}

.contact-button:hover span.text {
    color: #2d2b2b;
}

.submit-button {
    margin-top: 10px;
    background: transparent;
    border: none;
    outline: none;
    color: #8a8484;
    cursor: pointer;
    font-size: 1rem;
}

.submit-button:hover {
    color: #2d2b2b;
}

.scroll__wrapper {
    display: flex;
    place-items: center;
    transform: translate(80%, 80%);
    position: relative;
    color: var(--color-tem);

    &::after {
        content: 'scroll down';
        position: absolute;
        top: 0;
        left: -8px;
    }

    display: none;
}

.scroll__down {
    font-size: 4rem;
    animation: scroll 2s ease-in alternate-reverse infinite;
}

.scroll__down:hover .scroll__wrapper::after {
    display: block;
}

@media screen and (min-width: 1200px) {
    .scroll__wrapper {
        display: block;
    }
}

@keyframes scroll {
    0% {
        transform: translateY(5px);
    }

    50% {
        transform: translateY(-5px);
    }

    100% {
        transform: translateY(5px);
    }
}

@media screen and (min-width: 2000px) {
    .intro-page h1 {
        font-size: 10rem;
        line-height: 9rem;
        letter-spacing: -4px;
    }

    .intro-page h1 .home-logo {
        width: 100px;
    }

    .section-1 .text-desc {
        font-size: 1.5em;
    }
}

// extra large screens

@media screen and (max-width: 1900px) {
    .intro-page h1 {
        font-size: 6rem;
        line-height: 5rem;
        letter-spacing: -2px;
    }

    .intro-page h1 .home-logo {
        width: 60px;
    }

    .section-1 .text-desc {
        font-size: 1rem;
    }
}

// large screens

@media screen and (max-width: 1024px) {
    .intro-page h1 {
        font-size: 5.5rem;
        line-height: 5rem;
        letter-spacing: -2px;
    }

    .intro-page h1 .home-logo {
        width: 60px;
    }

    .section-1 .text-desc {
        font-size: 0.9rem;
    }

    .contact-button {
        max-width: 220px;
        height: 50px;
        line-height: 50px;
    }
}

// tablet screens || medium screens
@media screen and (max-width: 768px) {
    .section-1 {
        min-height: 100vh;
    }

    .intro-page h1 {
        font-size: 3.8rem;
        line-height: 3.5rem;
        letter-spacing: -2px;
    }

    .intro-page h1 .home-logo {
        width: 50px;
    }

    .section-1 .text-desc {
        font-size: 0.875rem;
    }

    .contact-button {
        max-width: 170px;
        height: 50px;
        line-height: 50px;
    }

    .section-1 .float-text {
        left: 20vw;
    }
}

@media screen and (max-width: 450px) {
    .section-1 {
        min-height: 100vh;
    }

    .intro-page h1 {
        font-size: 3rem;
        line-height: 2.3rem;
        letter-spacing: -1px;
    }

    .intro-page h1 .home-logo {
        width: 30px;
    }
}

// max-width: 450px smaller screens
@media screen and (max-width: 450px) {
    .intro-page h1 {
        font-size: 2.4rem;
        line-height: 2.3rem;
        letter-spacing: -1px;
    }

    .intro-page h1 .home-logo {
        width: 25px;
    }

    .section-1 .text-desc {
        font-size: 0.87em;
    }

    .contact-button {
        max-width: 180px;
        height: 45px;
        line-height: 45px;
        font-size: 0.95rem;
    }
}